.main {
  margin-top: 50px;
  padding: 20px;
  transition: all 200ms cubic-bezier(0.22, 0.61, 0.36, 1); }
  @media only screen and (min-width: 426px) {
    .main {
      margin-left: 60px; } }

.head {
