  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  height: 50px; }
  @media only screen and (min-width: 769px) {
    .head {
      z-index: 1; } }

.navigation {
  position: fixed;
  top: 0;
  bottom: 0;
  width: 200px;
  left: -220px;
  transition: all 200ms cubic-bezier(0.22, 0.61, 0.36, 1); }
  @media only screen and (min-width: 426px) {
    .navigation {
