      left: 0;
      overflow: hidden;
      width: 60px;
      z-index: 2; } }

.loading {
  position: absolute;
  height: 5px;
  top: 0;
  left: 0;
  width: 100%;
  background: rgba(0, 0, 0, 0.1);
  overflow: hidden; }

@keyframes load {
  0% {
    width: 100%;
    transform: translateX(-445px);
    opacity: 1; }
  100% {
    width: 20%;
    transform: translateX(445px);
    opacity: 0.8; } }
  .loading__bar {
    position: absolute;
    height: 5px;
    background: #005f27;
    animation: load infinite 1s ease; }

.loginbody {
  background: white; }
  @media only screen and (min-width: 426px) {
    .loginbody {
      background: url("../images/pattern-geo.jpg") center;
      background-size: 70px; } }

.login {
  padding: 30px;
  padding-top: 50px;
  max-width: 425px;
  min-width: 320px;
  margin: auto;
  background: white;
  height: 100vh; }
  @media only screen and (min-width: 426px) {
    .login {
