.logo {
  flex-wrap: wrap;
  height: 60px; }
  .logo__pic, .logo__text {
    width: 250px; }
    .logo__pic img, .logo__text img {
      width: 100%; }
  .logo__text {
    margin-top: 5px;
    width: 150px;
    transform: translateY(20px);
    opacity: 0; }

.loginForm {
  margin-top: 20px;
